import fs from 'fs';
import path from 'path';
import { InstitutionStat, CourseData } from '@/lib/types';
import InstitutionAnalysisClient from './InstitutionAnalysisClient';

import Papa from 'papaparse'; // PapaParse 임포트

async function getInstitutionStatsData(): Promise<{ initialInstitutionStats: InstitutionStat[]; availableYears: number[]; originalData: CourseData[] }> {
  const filePath = path.join(process.cwd(), 'public', 'processed-data', 'institution-stats.json');
  const originalDataPath = path.join(process.cwd(), 'kdt_dataset_202506.csv'); // 원본 데이터 경로를 CSV로 변경

  try {
    const fileContents = fs.readFileSync(filePath, 'utf8');
    const initialInstitutionStats: InstitutionStat[] = JSON.parse(fileContents);

    const originalDataContents = fs.readFileSync(originalDataPath, 'utf8');
    const parsedCsv = Papa.parse(originalDataContents, { header: true, skipEmptyLines: true });
    const originalData: CourseData[] = parsedCsv.data as CourseData[]; // CSV 파싱 결과 사용

    // 사용 가능한 연도 추출 (2021년부터 2026년까지 고정)
    const availableYears = [2021, 2022, 2023, 2024, 2025, 2026];

    return { initialInstitutionStats, availableYears };
  } catch (error) {
    console.error('Error loading institution stats data:', error);
    return { initialInstitutionStats: [], availableYears: [] };
  }
}

export default async function InstitutionAnalysisPage() {
  const { initialInstitutionStats, availableYears } = await getInstitutionStatsData();

  return (
    <InstitutionAnalysisClient
      initialInstitutionStats={initialInstitutionStats}
      availableYears={availableYears}
    />
  );
}
